.ingredients_wrap {
  display: flex;
  flex-direction: column;
  gap: 20px;

  .title {
    font-family: Mulish;
    font-size: 20px;
    font-weight: 800;
    line-height: 24px;
    letter-spacing: -0.02em;
    text-align: left;
    text-transform: uppercase;
    color: #050505;
    margin: 0;
    padding: 0;
  }

  .ingredients_list_wrap {
    ul.ingredients_list {
      display: flex;
      flex-wrap: wrap;
      gap: 16px;
    }
  }
}

@media screen and (min-width: 768px) {
  .ingredients_wrap {
    display: flex;
    .ingredients_list_wrap {
      ul.ingredients_list {
        gap: 20px;
      }
    }
  }
}

